var duration = 400;
var easeInOutQuad = function (progress) {
    return progress < 0.5 ? 2 * progress * progress : 1 - Math.pow(-2 * progress + 2, 2) / 2;
};
export var animate = function (callback, config) {
    var from = config.from, to = config.to, onRest = config.onRest;
    var start;
    var step = function () {
        var now = performance.now();
        if (!start)
            start = now;
        var progress = (now - start) / duration;
        if (progress < 1) {
            var value = ((to - from) * easeInOutQuad(progress) + from).toFixed(2);
            callback(value);
            window.requestAnimationFrame(step);
        }
        else {
            if (onRest)
                onRest();
        }
    };
    window.requestAnimationFrame(step);
};
